import { createApp } from 'vue'
import { createStore } from 'vuex'
import App from './App.vue'
import router from './router'
import BalmUI from 'balm-ui'
import 'balm-ui/dist/balm-ui.css'
import db from '@/components/firebaseInit'
import { collection, query, where, getDocs, orderBy } from 'firebase/firestore'

const store = createStore({
    state(){
        return{
            loading: true,
            appointments: [],
            cart: []
        }
    },
    mutations:{
        setLoadingStatus(state, status){
            state.loading = status
        },
        setAppointments(state, appointments){
            state.appointments = appointments
        },
        updateCart(state, item){
            state.cart.push(item)
            console.log(state.cart)
        },
        removeItem(state, index){
            state.cart.splice(index, 1)
        }
    },
    actions:{
        fetchAppointments(context) {
            context.commit('setLoadingStatus', true);
            const q = query(context.getters.appointmentsCollection, orderBy('date'), orderBy('start_index'));
            const appointments = [];
          
            return getDocs(q)
              .then((appointmentsSnapshot) => {
                appointmentsSnapshot.forEach((doc) => {
                  appointments.push(doc.data());
                });
                context.commit('setAppointments', appointments)
                context.commit('setLoadingStatus', false);
              })
              .catch((error) => {
                console.error('Error: ', error);
                context.commit('setLoadingStatus', false);
              });
        },
        addToCart(context, item){
            context.commit('updateCart', item)
        },
        removeFromCart(context, index){
            context.commit('removeItem', index)
        },
        toggleLoading(context, status){
            context.commit('setLoadingStatus', status)
        }
    },
    getters:{
        appointmentsCollection(){
            return collection(db, 'appointments')
        },
        allAppointments(state){
            return state.appointments
        },
        loadingStatus(state){
            return state.loading
        },
        cart(state){
            return state.cart
        }
    }
})


createApp(App).use(router).use(store).use(BalmUI).mount('#app')
